class Graph {
    constructor() {
        this.vertices = []; //
        this.adjList = {}; //
        this.marked = {};//
        this.inStack = {};
        this.wayNumber = 0;
        this.edgeTo = [];//  
    }

    addVertex(v) { //
        this.vertices.push(v);
        this.marked[v] = false;
        this.inStack[v] = false;
        if (!this.adjList[v]) this.adjList[v] = {};
    }

    addEdge(v, w, n = 1) { //
        this.adjList[v][w] = n; //
        //this.adjList[w][v] = n; //
    }

    bfs(s) {
        let queue = [];
        this.marked[s] = true;
        queue.push(s);//push
        while (queue.length > 0) {
            let v = queue.shift();
            // if (v != undefined) {
            //     console.log("" + v);debugger;
            // }
            for (let node in this.adjList[v]) {
                if (!this.marked[node]) {
                    this.edgeTo[node] = v;//  
                    this.marked[node] = true;//
                    queue.push(node);//  
                }
            }
        }
    }

    dfs(s){
        this.marked[s] = true;
        this.inStack[s] = true;
        console.log(s);
        for (let node in this.adjList[s]) {
            if (!this.marked[node]) {
                this.dfs(node);
            }else if(this.inStack[node]){
                console.log("")
                return false;
            }
        }
        this.inStack[s] = false;
    }

    //dfs
    //node === e =
    findPath(s,e){
        this.inStack[s] = true;
        for (let node in this.adjList[s]) {
            if(node === e){
                this.wayNumber++;
                for(let res in this.inStack){
                    if(this.inStack[res]){
                        console.log(res);
                    }
                }
                console.log(node);//res
                this.inStack[s] = false;
                return;
            }
            this.findPath(node,e);
        }
        this.inStack[s] = false;
    }

    shortestPath(source, target) {
        let path = [];
        for (let i = target; i != source; i = this.edgeTo[i]) {//  
            path.push(i);
        }
        path.push(source);//  
        //console.log(path);
        return path;
    }
}


class Dijkstra {
    constructor(map) {
        this.map = map;
    }

    findShortestPath(start, end, map = this.map) {
        let costs = {},
            tempList = { '0': [start] },
            predecessors = {},
            nodes = [],
            tempEnd = end,
            mapArr = [],
            ListArr = [];
        for (let key in map) {
            mapArr.push(key);
        }
        costs[start] = 0;
        //console.log(' ' + start)
        while (tempList) {
            let keys = [],
                List = '';
            for (let key in tempList) {
                Object.prototype.hasOwnProperty.call(tempList, key) && keys.push(key);
                List += (' ' + tempList[key] + '(' + key + ')');
            }
            if (List != '')
                //console.log('List' + List);
                if (!keys.length) break;
            keys.sort((a, b) => a - b);//

            let first = keys[0],
                bucket = tempList[first],
                node = bucket.shift(),
                currentCost = parseFloat(first),
                adjacentNodes = map[node] || {};
            if (ListArr.indexOf(node) != -1) continue;//
            //console.log('' + node)
            ListArr.push(node);

            if (ListArr.length == mapArr.length) break;//
            if (!bucket.length) delete tempList[first];
            // for (let key in tempList) {
            //     console.log('List ' + tempList[key] + '(' + key + ')');
            // }
            //console.log(node + ' ')
            for (let vertex in adjacentNodes) {
                if (Object.prototype.hasOwnProperty.call(adjacentNodes, vertex)) {
                    var cost = adjacentNodes[vertex],
                        totalCost = cost + currentCost,
                        vertexCost = costs[vertex];//

                    if ((vertexCost === undefined) || (vertexCost > totalCost)) {//
                        //console.log('' + vertex + '' + totalCost);
                        costs[vertex] = totalCost;
                        if (!tempList['' + totalCost]) tempList['' + totalCost] = [];
                        tempList['' + totalCost].push(vertex);//
                        predecessors[vertex] = node;//
                    } else {
                        //console.log('' + node + '' + vertex + '' + totalCost + '' + vertexCost + '')
                    }
                }
            }
        }
        //console.log('' + start + ',');
        //console.log(costs);
        while (tempEnd !== undefined) {
            nodes.push(tempEnd);
            tempEnd = predecessors[tempEnd];
        }
        nodes.reverse();//
        console.log('' + start + '' + end + '' + nodes.join('=>'));

    }

}

class Floyd {
    constructor(map) {
        this.map = map;
    }
    
    findShortestPath(start, end, map = this.map) {
        let inf = 99;
        for (let k in map) {
            for (let i in map) {
                for (let j in map) {
                    if(map[i][j]===undefined)map[i][j] = inf;
                    if (map[i][k] != undefined && map[k][j] != undefined && map[i][j] > map[i][k] + map[k][j]){
                        map[i][j] = map[i][k] + map[k][j];
                    }
                }
            }
        }
        console.log(map[start][end]);
    }
}
export { Graph, Dijkstra, Floyd }


